var moment = require('moment')
var numeral = require('numeral')
var _ = require('lodash')

export default {
  install: function (Vue) {
    /**
     * Vue filter to convert the given value to formated.
     *
     * @param {String} value    The value string.
     * @param {Number} decimals The number of decimal places.
     */
    Vue.filter('dateFormat', function (value, dateFormat) {
      if (!value) {
        return ''
      }

      if (!dateFormat) {
        dateFormat = 'DD/MM/YYYY'
      }

      return moment(value).format(dateFormat)
    })

    /**
     * Vue filter to convert the given value to formated.
     *
     * @param {String} value    The value string.
     * @param {Number} decimals The number of decimal places.
     */
    Vue.filter('numerize', function (value, nullWhenZero, numberFormat) {
      value = Number(value) || 0
      if (value === 0) {
        if (nullWhenZero) {
          return ''
        } else {
          value = 0
        }
      }

      if (!numberFormat) {
        numberFormat = '0,0.00'
      }

      return numeral(value).format(numberFormat)
    })

    /**
     * Vue filter to convert the given value absolute.
     *
     * @param {Number} value    The value string.
     */
    Vue.filter('absolute', function (value) {
      return Math.abs(value)
    })

    /**
     * Vue filter to convert the given value to percent.
     * http://jsfiddle.net/bryan_k/qauf3qyh/
     *
     * @param {String} value    The value string.
     * @param {Number} decimals The number of decimal places.
     */
    Vue.filter('percentage', function (value, decimals) {
      if (!value) {
        value = 0
      }

      if (!decimals) {
        decimals = 0
      }

      value = value * 100
      value = Math.round(value * Math.pow(10, decimals)) / Math.pow(10, decimals)
      value = value + '%'
      return value
    })

    /**
     * Vue filter to round the decimal to the given place.
     * http://jsfiddle.net/bryan_k/3ova17y9/
     *
     * @param {String} value    The value string.
     * @param {Number} decimals The number of decimal places.
     */
    Vue.filter('round', function (value, decimals) {
      if (!value) {
        value = 0
      }

      if (!decimals) {
        decimals = 0
      }

      value = Math.round(value * Math.pow(10, decimals)) / Math.pow(10, decimals)
      return value
    })

    /**
     * Vue filter to convert the given value to whole dollars, no change.
     * http://jsfiddle.net/bryan_k/2t6bqqfc/
     *
     * @param {String} value The value string.
     */
    Vue.filter('no-change', function (value, symbol) {
      if (!value) {
        value = 0
      }

      if (!symbol) {
        symbol = '$'
      }

      value = value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',').split('.')[0]
      return symbol + value
    })

    /**
     * Vue filter to make a simple timestamp for an ISO date.
     * http://jsfiddle.net/bryan_k/44kqtpeg/
     *
     * @param {String} value The value string.
     */
    Vue.filter('timestamp', function (value) {
      var parts = value.split(' ')
      var date = parts[0]
      var time = parts[1]
      var hour

      date = date.split('-')
      time = time.split(':')

      if (parseInt(time[0], 10) > 12) {
        hour = parseInt(time[0], 10) % 12
      } else {
        hour = parseInt(time[0], 10)
      }

      hour = hour < 10 ? '0' + hour : hour

      return '[' + date[1] + '/' + date[2] + ' ' + hour + ':' + time[1] + ']'
    })

    /**
     * Vue filter to truncate a string to the specified length.
     * @param {String} value The value string.
     */
    Vue.filter('truncate', function (value, length) {
      if (value.length < length) {
        return value
      }

      length = length - 3

      return value.substring(0, length) + '...'
    })

    Vue.filter('levelPad', function (value, path, offset = 0, spaces = 6) {
      if (!path) {
        return value
      }
      let level = path.split ? path.split('.').length : 0
      return _.repeat(_.repeat(' - ', spaces), level)
    })
  }
}



